 .refrees{
    background: url("../../../assets/media/pattern.jpg") no-repeat center center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    align-items: center; 
 }
  .refrees > .container{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }
  .refrees > .container h1{
     font-size: 1.3em;
     font-weight: bold;
     position: relative;
     z-index: 3;
     margin: 2em 0;
 }
 .refrees > .container h1::after{
    width: 130px;
    height: 6px;
    content: "";
    background-color: #DFC8A3;
    opacity: 0.9;
    padding: 8px; 
    position: absolute;
    left: -5%;
    bottom: -5px;
   z-index: -1;
   
 }
 .wrapper{
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: space-around;
     width: 100%;
     margin: 4em auto;
 }
  
 .mainCarousel{
     display: flex;
     flex-direction: row;
     width: 80%;
     height: 400px;
 }
.mainCarousel:nth-child(1){
    padding-bottom: 2em;
}
 
.refrees__btn{
    width: 14em;
}
.icons{
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media screen and (max-width : 580px) {

    .refrees > .container{
      margin: 0;
      padding: 0;
    }
    .wrapper{
        margin: 0 auto;
        padding: 0;
    }
    .wrapper > div{
        width: 60%;
    }
    

.slick-slider{
    width: 240px !important;
    margin: 0 auto;
}
    
    .wrapper > .refrees-carousel{
        width: 400px;
    }
    .refrees > .container h1{
        margin: 2em;
    }
}



/* @media screen and (max-width : 576px) {
 .refrees {
  height: 70vh;
}
} */
@media screen and (max-width : 768px) {
    .refrees {
        height: auto;
    }
    .wrapper{
        margin: 2em auto;
    }
    .refrees > .container h1{
        margin: 0;
    }
}
@media screen and (max-width : 400px) {
    .refrees{
        height: auto;
    }
    .refrees > .container h1{
        margin: 1em auto;
    }
    .refrees > .wrapper > .carousel{
        width: 300px !important;
      }
      .refrees > .carousel{
        width: 66%;
      }
    .wrapper{
        margin: 1em auto;
    }
 .icons {
 display: none;
}
.slick-slider{
    width: 100%;
}
