.hamian {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(white, #fafafa);
}
.hamian > .container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hamian h1 {
  position: relative;
  font-size: 2em;

  z-index: 3;
}
.hamian h1::after {
  width: 110px;
  height: 6px;
  content: "";
  background-color: #9de0d6;
  opacity: 0.9;
  padding: 8px;
  position: absolute;
  right: 0px;
  bottom: -5px;
  z-index: -1;
}
.list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1em;
  margin-top: 6em;
  align-items: center;
  justify-content: center;
}
.item {
  width: 8em;
  height: 8em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.item img {
  object-fit: contain;
  width: 5em;
  height: 5em;
}
@media screen and (max-width: 1200px) {
  /* .item {
 width: 10em;
 height: 10em;
} */
}
@media screen and (max-width: 992px) {
  .item {
    width: 7em;
    height: 7em;
  }
}
@media screen and (max-width: 768px) {
  .hamian {
    height: auto;
    padding-bottom: 3em;
  }
  .list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 4em;
    margin-top: 2em;
  }
  .item {
    width: 3em;
    height: 3em;
  }
  .item img {
    width: 4em;
    height: 4em;
  }
}
@media screen and (max-width: 576px) {
  .hamian {
    width: 100%;
    padding-bottom: 3em;
  }
  .list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 2em;
    margin-top: 3em;
  }
  .item img {
    width: 3em;
    height: 3em;
  }
}
@media screen and (max-width: 375px) {
  .hamian {
    width: 100%;
  }
  .list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 2em;
    margin: 3em;
  }
}
