.refreeCard {
  width: 13em;
  height: 18em;
  transition: 0.2s;
}

.refreeCard.-fade {
  opacity: 0.5;
}
.refreeCard.-odd {
  position: relative;
  padding-top: 3rem;
  height: 21em;
}

.refreeCard > div {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: right;
  border: 1px solid #eee;
  color: #000;
  background-color: white;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  transition: 0.8s;
  margin: 0 auto;
}
.image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.image img {
  width: 100%;
  height: 100%;
}
.refreeCard-info {
  display: flex;
  flex-direction: column;
}
.refreeCard-info span:first-child {
  color: #000;
  padding: 0.8em;
}
ion-icon {
  text-align: right;
  margin-left: auto;
  padding: 0 0.4em;
  font-size: 1.5em;
}
.refreeCard-info .more-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: 0.4s;
  transform: translateX(60%);
}
.refreeCard:hover .refreeCard-info .more-info {
  transform: translateX(10%);
}
.refreeCard-info .more-info small {
  color: #c4c4c4;
}

.refreeCard:hover .refreeCard-info span small {
  display: flex;
}
.refreeCard:hover .refreeCard-info span ion-icon {
  color: #9de0d6;
}
.refreeCard-info span:last-child {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-right: 2em;
  transition: 0.8s;
}
